#mainContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  
  #textContainer {
    max-width: 400px;
  }
  
  #recordButtonContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    padding: 20px;
  }
  
  .recordActive {
    cursor: pointer;
    background-color: red;
    color: white;
    margin: 10px;
  }
  
  .recordInactive {
    cursor: pointer;
    background-color: white;
    margin: 10px;
  }
  
  .recordInactive:hover {
    background-color: lightgray;
  }
  
  .recordInactive:active {
    background-color: white;
  }
  
  #headerText {
    text-align: center;
    width: 100%;
    margin-top: 20px;
  }
  
  #transcribedText,
  #translatedText {
    width: 100px;
    background-color: rgb(240, 240, 240);
    border: 1px solid darkgray;
    min-height: 100px;
    padding: 20px;
    margin: 10px 0;
  }